* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url("media/bg.jpeg") center center no-repeat;
  background-size: cover;
  min-height: 100vh;
  padding: 2rem 0;
}

.App {
  text-align: center;
}

.prompt-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.score {
  font-size: 6rem;
  border-radius: 16px;
}

.score-success {
  text-shadow: 0px 0px 9px #198754;
}

.score-danger {
  text-shadow: 0px 0px 9px #dc3545;
}
